  const _bindGridAttachment = (viewmodel, billNo) => {
    const addAttachment = viewmodel.get('addAttachment');
    if (!addAttachment) {
      return;
    }
    const gridModels = viewmodel.getGridModels();
    gridModels.forEach(gridModel => {
      gridModel.on('beforeSetDataSource', function (dataList) {
        dataList.forEach(item => {
          if (!item.attachmentId) {
            if (item.id) {
              item.attachmentId = item.id
            } else {
              item.attachmentId = uuid();
            }
          }
        });
        return dataList;
      });
    })
  }
  const _bindGridDblClick = (viewmodel, billNo) => {
    const gridModel = viewmodel.getGridModel()
    if (!gridModel) return
    let dblClickItemName = null;
    const columns = gridModel.getColumns();
    for (const attr in columns) {
      if (columns[attr].bJointQuery) {
        dblClickItemName = attr;
        break;
      }
    }
    gridModel.on('cellJointQuery', function (args) {
      const rowData = gridModel.getRow(args.rowIndex);
      if (args.cellName === dblClickItemName) {
        gridModel.execute('dblClick', rowData, args);
        gridModel.setCache('backData', { rowIndex: args.rowIndex, id: rowData.id });
      } else {
        gridModel.execute('jointQuery', Object.assign(args, { rowData }));
      }
    });
    gridModel.on('dblClick', function (args, cellInfo) {
      const parent = this.getParent()
      let params = {
        mode: env.VOUCHER_STATE_EDIT,
        readOnly: !viewmodel.getParams().doubleEdit,
        id: args.id,
        rowData: args,
        cellInfo,
        carryParams: {},
        cardViewModel: parent.getCache('cardViewModel')
      }
      params = Object.assign({}, params, viewmodel.getCache('lastSearchCondition'))
      if (!gridModel.execute('beforeDblClick', params)) return true
      const cardKey = common.getVmParams(viewmodel, 'cardKey')
      if (!cardKey)
        return
      if (themeType == 'ncc') {
        const rows = gridModel.get('dataSource'); // 
        const index = rows.findIndex(item => item.id == args.id);
        parent.setCache('curVoucherIndex', index);
      }
      const dt = {
        billtype: 'yyarchive',
        billno: cardKey,
        params: params
      }
      cb.loader.runCommandLine('bill', dt, parent)
    })

    // TODO  (
    viewmodel.on('afterBatchdelete', res => {
      const allGridmodel = viewmodel.getGridModels();
      if (!res.res.failCount) {
        for (let i = 1; i < allGridmodel.length; i++) {
          allGridmodel[i].clear();
        }
      }
    });
    gridModel.on('afterSetDataSource', () => {
      const afterAct = gridModel.getCache('afterAct');
      if (afterAct)
        afterAct();
      common.setGridActionStates(gridModel, viewmodel);
    });
  }
