  const batchDelete = (billNo, viewModel, params, beforeAct, afterAct) => {
    if (themeType == 'ncc') {
      batchDo(billNo, viewModel, params, beforeAct, afterAct);
    } else {
      batchDo(billNo, viewModel, params, beforeAct, afterAct, true);
    }
  }

  const batchDo = (billNo, viewmodel, params, beforeAct, afterAct, confirm) => {
    // 
    if (params.cItemName !== 'btnDelete' && params.cItemName !== 'btnDeleteRow') {
      if (confirm == undefined) {
        confirm = !!cb.extendConfig.allConfirm;
      }
    }
    let primaryKeyField;
    if (params.cmdParameter) {
      try {
        primaryKeyField = JSON.parse(params.cmdParameter).primarykey;
      } catch (e) {
        console.error('batchDo action parameter error: ' + e.message);
      }
    }
    if (!primaryKeyField)
      primaryKeyField = 'id';
    const gridModel = viewmodel.getGridModel();
    const treeModel = viewmodel.getTreeModel();
    let selectDatas;
    let cParameter = {};
    params = params || {};
    if (params.cParameter) { try { cParameter = JSON.parse(params.cParameter); } catch (e) { } }
    if (cParameter.billnum && cParameter.type === 'tree') {
      selectDatas = treeModel.getSelectedNodes();
      billNo = cParameter.billnum
    } else if (gridModel) {
      selectDatas = params && params.params && params.params.index != null
        ? [gridModel.getRow(params.params.index)]
        : gridModel.getSelectedRows();
    } else if (treeModel) {
      selectDatas = treeModel.getSelectedNodes();
    }
    if (!selectDatas || selectDatas.length == 0) {
      cb.utils.alert('', 'warning')
      return
    }
    let data;
    if (cParameter.billnum && cParameter.type === 'tree') {
      data = treeModel.getNecessaryDataForRows(selectDatas);
    } else if (gridModel) {
      data = gridModel.getNecessaryDataForRows(selectDatas);
    } else {
      data = treeModel.getNecessaryDataForRows(selectDatas);
    }
    const paramsData = []; const primaryKeys = [];
    selectDatas.forEach(function (row, index) {
      if (row.id && row.pubts) {
        Object.assign(data[index], {
          code: row.code,
          id: row.id,
          pubts: row.pubts
        })
      }
      if (primaryKeys.indexOf(row[primaryKeyField]) === -1) {
        paramsData.push(data[index]);
        primaryKeys.push(row[primaryKeyField]);
      }
    }, this)
    const postData = {
      billnum: billNo,
      data: JSON.stringify(paramsData)
    }
    const beforeActData = {
      params: params,
      data: postData
    }
    beforeAct(beforeActData, function () {
      if (confirm) {
        const text = viewmodel.getParams().caption;
        cb.utils.confirm(`${params.cShowCaption}${text}`, function () {
          _batchDo(viewmodel, params, afterAct, paramsData, postData, primaryKeyField);
        }, function () {
          if (params && params.enabledCallback)
            params.enabledCallback();
        });
      } else {
        _batchDo(viewmodel, params, afterAct, paramsData, postData, primaryKeyField);
      }
    })
  }

  const _batchDo = (viewmodel, params, afterAct, selectDatas, postData, primaryKeyField) => {
    const callback = function (err, res) {
      if (!err) {
        const actionText = params.cShowCaption;
        if (selectDatas.length === 1) {
          if (res.failCount) {
            cb.utils.alert(`${actionText}${res.messages[0]}`, 'error');
          } else {
            cb.utils.alert(`${actionText}`, 'success');
          }
          // } else if (true || "batchaudit,batchunaudit".indexOf(params.cAction.toLowerCase()) >= 0) {
        } else {
          if (cb.extendConfig.theme === 'ncc' && !viewmodel.getCache('showBatchMsg')) {
            if (res.failCount > 0) {
              cb.utils.alert(`${actionText}${res.count}, ${res.sucessCount},${res.failCount}`, 'error')
            } else {
              cb.utils.alert(`${actionText}${res.count}, ${res.sucessCount}`, 'success')
            }
          } else {
            viewmodel.communication({
              type: 'modal',
              payload: {
                key: 'BatchMsg',
                data: {
                  type: 2,
                  res,
                  //   model: viewmodel,
                  // selectDatas,
                  // bWhole: (primaryKeyField.toLowerCase() == "id") ? true : false,
                  // dataType: "yyarchive"
                }
              }
            });
          }
        }
        // else {
        //     cb.utils.alert(`${res.count}, ${res.sucessCount},${res.failCount}`)
        //     if (res.failCount > 0) {
        //         console.log('fail message')
        //         console.dir(res.messages)
        //     }
        // }
        viewmodel.execute('back', params);
      }
      const afterActData = {
        err: err,
        res: res
      }
      afterAct(afterActData, common.afterAct)
    }
    common.doProxy(params, callback, postData)
  }

  const dblClick = (billNo, viewmodel, para, beforeAct, afterAct) => {
    const gridModel = viewmodel.getGridModel()
    let id, rowData;
    let cParameter = {};
    let billno;
    para = para || {};
    if (para.cParameter) { try { cParameter = JSON.parse(para.cParameter); } catch (e) { } }
    if (cParameter.billnum && cParameter.type === 'tree') {
      billno = cParameter.billnum
      const node = _getTreeSelectNode(viewmodel);
      id = node.id;
      rowData = node;
    } else if (gridModel) {
      const rowIndex = para && para.params && para.params.index;
      const selectDatas = rowIndex != null ? [gridModel.getRow(para.params.index)] : gridModel.getSelectedRows();
      if (!selectDatas || selectDatas.length !== 1) {
        cb.utils.alert('', 'warning');
        return;
      }
      id = selectDatas[0].id;
      rowData = selectDatas[0];
      gridModel.setCache('backData', { rowIndex, id });
      billno = common.getVmParams(viewmodel, 'cardKey')
    } else {
      const node = _getTreeSelectNode(viewmodel);
      if (!node) {
        cb.utils.alert('', 'warning');
        return;
      }
      id = node.id;
      rowData = node;
      billno = common.getVmParams(viewmodel, 'cardKey')
    }

    let params = {
      params: para,
      mode: env.VOUCHER_STATE_EDIT,
      copy: para.copy,
      id,
      rowData,
      carryParams: {}
    }
    //  
    if (gridModel && themeType == 'ncc') {
      const rows = gridModel.get('dataSource'); // 
      const index = rows.findIndex(item => item.id == id);
      viewmodel.setCache('curVoucherIndex', index);
      params = Object.assign({}, params, viewmodel.getCache('lastSearchCondition'))
    }
    beforeAct({
      params: para,
      carry: params
    }, function () {
      const dt = {
        billtype: 'yyarchive',
        billno: billno,
        params: params
      }
      cb.loader.runCommandLine('bill', dt, viewmodel)
      afterAct()
    })
  }
  const edit = (billNo, viewmodel, para, beforeAct, afterAct) => {
    const billType = viewmodel.getParams() && viewmodel.getParams().billType
    if (billType) {
      if (_hasList(billType)) { // ArchiveList,TreeList,yylist,compare
        dblClick(billNo, viewmodel, para, beforeAct, afterAct)
      } else {
        common.edit(billNo, viewmodel, para, beforeAct, afterAct)
      }
    }
  }

  const refresh = (billNo, viewmodel, para, beforeAct, afterAct) => {
    const params = viewmodel.getCache('lastSearchCondition');
    const treeModel = viewmodel.getTreeModel()
    const gridModel = viewmodel.getGridModel();
    if (!gridModel) {
      if (cb.extendConfig.theme == 'ncc') {
        // 
        if (!_validateMustInput(viewmodel) || !params) {
          return;
        }
      }
      queryTree(billNo, viewmodel, params);
      return;
    }
    const obj = Object.assign({
      billnum: billNo
    }, para);
    const isSum = viewmodel.getCache('isSum')
    if (isSum != null)
      obj.isSum = isSum
    Object.assign(obj, viewmodel.getParams().query);
    const groupSchemaId = viewmodel.getCache('groupSchemaId');
    if (groupSchemaId)
      obj.groupSchemaId = groupSchemaId;
    if (treeModel) {
      const node = _getTreeSelectNode(viewmodel)
      if (node) {
        const { path, id } = node;
        if (path && id) {
          obj.path = path
          obj.ids = id.toString();
        }
        else if (path)
          obj.path = path;
        else if (id)
          obj.ids = id.toString();
      }
    }
    const data = cb.utils.extend(true, obj, params)
    const beforeActData = {
      params: params,
      data: data
    }
    if (beforeAct) {
      beforeAct(beforeActData, function () {
        _loadGrid(viewmodel, data, afterAct)
      })
    } else {
      _loadGrid(viewmodel, data, afterAct)
    }
  }

