  const preview = (billNo, viewmodel, params, beforeAct, afterAct) => {
    beforeAct({ params }, function () {
      const template = viewmodel.get('templates').getValue();
      if (!template) {
        cb.utils.alert('', 'warning');
        return;
      }
      const newTab = window.open('about:blank');
      const proxy = cb.rest.DynamicProxy.create({
        preview: { url: 'print/printPreview', method: 'POST' }
      })
      const ids = _getSelectRowIds(viewmodel.getGridModel())
      const condition = viewmodel.getCache('lastSearchCondition') ? JSON.stringify(viewmodel.getCache('lastSearchCondition').condition) : null;
      const args = {
        route: params.cSvcUrl || '/bill',
        billno: billNo,
        template: template,
        ids: ids,
        condition: condition
      };
      proxy.preview(args, function (err, result) {
        if (!err) {
          const random = uuid();
          const queryString = new cb.utils.queryString(result);
          queryString.set('random', random);
          queryString.set('params', null);
          newTab.location.href = queryString.pathname + decodeURIComponent(queryString.toStr());
          window.addEventListener('message', (event) => {
            if (event.data.key === 'context' && event.data.random === random) {
              args.tenantId = queryString.get('tenantId');
              newTab.postMessage({ key: 'params', value: args }, event.origin);
            }
          }, false);
        }
        const afterActData = {
          err: err,
          res: result
        };
        afterAct(afterActData, common.afterAct);
      });
    });
  }
