import mtl from 'mtl-js-sdk'

function _getParams (params) {
  let data = {};
  if (params) {
    if (params.cParameter) {
      data = JSON.parse(params.cParameter);
    } else {
      data = params;
    }
  }
  return data;
}

function _showPrompt (result) {
  if (mtl.platform !== 'upesn') {
    cb.utils.alert(' ' + mtl.platform + ' ' + result);
  } else {
    console.log(' ' + mtl.platform + ' ' + result);
  }
}

export function mtlScan (billNo, viewmodel, params, beforeAct, afterAct) {
  const data = {};
  if (params?.cParameter) {
    const newParams = JSON.parse(params.cParameter);
    data.needResult = newParams.needResult ? parseInt(newParams.needResult) : 1;
    if (newParams.scanType === 'all') {
      data.scanType = ['qrCode', 'barCode'];
    } else {
      if (!Array.isArray(data.scanType)) {
        data.scanType = [newParams.scanType];
      }
    }
  }
  mtl.scanQRCode({
    ...data,
    success: function (res) {
      const result = res.resultStr;
      cb.invoker.publish('mtl', { action: 'scan', result: result });
      _showPrompt(result);
    },
    fail: function (err) {
      const message = err.message; // 
      _showPrompt(message);
    }
  });
};

