export function mtlChooseImage (billNo, viewmodel, params, beforeAct, afterAct) {
  const data = {};
  if (params?.cParameter) {
    const newParams = JSON.parse(params.cParameter);
    data.count = newParams.count;
    if (newParams.sourceType === 'all') {
      data.sourceType = ['album', 'camera'];
    } else {
      if (!Array.isArray(data.sourceType)) {
        data.sourceType = [newParams.sourceType];
      }
    }
  }
  mtl.chooseImage({
    ...data,
    success: function (res) {
      // IDlocalIdimgsrc
      const localIds = res.localIds; // ['wxLocalResource://imageid123456789987654321', ...]
      cb.invoker.publish('mtl', { action: 'chooseImage', result: localIds });
      _showPrompt(res);
    },
    fail: function (err) {
      const message = err.message; // 
      _showPrompt(message);
    }
  });
}

